import { Component, Input, ElementRef, OnInit, OnDestroy, Output, EventEmitter, ViewChild } from '@angular/core';

@Component({
  selector: 'gallery',
  template: `<div class="gallery" *ngIf="render" (click)="wrapClickAction($event)" [ngClass]="{'open':visible,
    'gallery-has-footer':(isToolsBar+'')!='false',
    'gallery-has-header':(isHeader+'')!='false',
     'gallery-fade-in':(isAnimation+'')!='false'&&!isEventSource,
     'gallery-inc-in':(isAnimation+'')!='false'&&isEventSource,
     'lg':size=='lg',
     'md':size=='md',
     'sm':size=='sm',
     'xs':size=='xs'
     }" [style.left]="isFullScreen?0:left" [style.top]="isFullScreen?0:top" [style.width]="isFullScreen?'100%':null" [style.height]="isFullScreen?'100%':null" [style.border]="isFullScreen?'none':null" [style.transition]="transition?transition:null">
    <div *ngIf="(isHeader+'')!='false'" class="gallery-header">
        {{title}}
        <a class="gallery-btn-full" *ngIf="size" [ngClass]="{'resize':isFullScreen}" (click)="toggleFullScreen()">
        </a>
        <a class="gallery-btn-close" (click)="close()">×</a>
    </div>
    <div class="gallery-body" #galleryBody>
        <div class="gallery-sliders" (click)="whiteSpaceClickAction()">
            <a class="gallery-img-container" *ngFor="let img of images;let i=index" [style.transform]="'rotate('+img.rotate+'deg)'" [style.left.%]="activeIndex==i?0:(i-activeIndex)*200" (mousemove)="onMouseMoveImg($event)">
                <img [src]="img.url" [hidden]="!ready&&activeIndex!=i" (click)="stopPropagation($event)" [style.transform]="'scale('+img.scale+')'" [style.left.px]="img.left" [style.top.px]="img.top" (mousedown)="onMouseDownImg($event)" (mouseup)="onMouseUpImg($event)"
                />
            </a>
        </div>
        <a class="gallery-close" (click)="close()" *ngIf="isShowBtns&&!((isHeader+'')!='false')">×</a>
        <a class="gallery-prev" (click)="prev();slideThumbAfterChange(0)" *ngIf="isShowBtns&&images.length>1">‹</a>
        <a class="gallery-next" (click)="next();slideThumbAfterChange(1)" *ngIf="isShowBtns&&images.length>1">›</a>
        <div class="gallery-thumb" *ngIf="isShowBtns&&images.length>1" [ngClass]="{'gallery-thumb-overflow':isThumbOverflow}">
            <div class="gallery-thumb-back" (click)="slideThumb(galleryThumbSliders,0)"></div>
            <div class="gallery-thumb-sliders" #galleryThumbSliders>
                <div class="gallery-thumb-scroll" [style.width]="isThumbOverflow?thumbScrollWidth+'px':'auto'">

                    <a class="gallery-thumb-slider" *ngFor="let img of images;let i=index" [ngClass]="{'active':i==activeIndex}" (click)="activate(i)">
                        <img [src]="img.url" [ngClass]="{'cover':isSupportCssObjectFit}" />
                    </a>
                </div>
            </div>
            <div class="gallery-thumb-forward" (click)="slideThumb(galleryThumbSliders,1)"></div>
        </div>
        <div class="gallery-scale-info" *ngIf="isShowScaleInfo&&images[activeIndex].scale!=1">
            {{images[activeIndex].scale|percent}}
        </div>
    </div>
    <div class="gallery-footer" *ngIf="(isToolsBar+'')!='false'">
        <div class="gallery-toolsbar">
            <a class="gallery-tool-item gallery-enlarge" (click)="enlarge(0.1)" title="放大">
            </a>
            <a class="gallery-tool-item gallery-reduce" (click)="reduce(0.1)" title="缩小">
            </a>
            <a class="gallery-tool-item gallery-rotate" (click)="rotate()" title="旋转"></a>
            <a class="gallery-tool-item gallery-download" *ngIf="(isBtnDownload+'')!='false'" (click)="download()" title="下载"></a>
        </div>
    </div>
</div>`,
  styles: [`.gallery{position:fixed;width:100%;height:100%;opacity:1;overflow:hidden;background-color:rgba(0,0,0,.75);z-index:99999;-webkit-transform:translate3d(0,0,0);bottom:36px}.gallery.gallery-fade-in{opacity:0}.gallery.gallery-fade-in.open{opacity:1}.gallery.gallery-inc-in{width:0;height:0;opacity:0}.gallery.gallery-inc-in.open{width:100%;height:100%;opacity:1}.gallery,.gallery *{-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.gallery-sliders{position:absolute;left:0;top:0;right:0;bottom:0;background-color:rgba(0,0,0,.75);text-align:center}.gallery-sliders img{max-width:100%;max-height:100%;position:absolute;left:0;top:0;right:0;bottom:0;margin:auto;-webkit-transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s,-webkit-transform .3s;transform:translate3d(0,0,0);-webkit-transform:translate3d(0,0,0);cursor:pointer}.gallery-btn-full span{display:block}.gallery .gallery-next,.gallery .gallery-prev{-webkit-box-sizing:content-box;box-sizing:content-box}.gallery-next,.gallery-prev{position:absolute;top:50%;left:15px;width:40px;height:40px;margin-top:-23px;font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;font-size:60px;font-weight:100;line-height:30px;color:#fff;text-decoration:none;text-shadow:0 0 2px #000;text-align:center;background:rgba(0,0,0,.5);-webkit-box-sizing:content-box;box-sizing:content-box;border:3px solid #fff;border-radius:23px;opacity:.5;cursor:pointer}.gallery-next{left:auto;right:15px}.gallery-next:hover,.gallery-prev:hover{color:#fff;opacity:1}.gallery-thumb{position:absolute;bottom:2px;width:100%;height:40px}.gallery-thumb img{max-width:100%;max-height:100%;position:absolute;left:0;top:0;right:0;bottom:0;margin:auto}.gallery-thumb img.cover{width:100%;height:100%;-o-object-fit:cover;object-fit:cover}.gallery-thumb-sliders{width:50%;height:40px;margin:0 auto;text-align:center;overflow:hidden;white-space:nowrap}.gallery-thumb-scroll{width:100%;height:100%}.gallery-thumb-slider{position:relative;display:inline-block;width:40px;height:40px;border:2px solid transparent;cursor:pointer}.gallery-thumb-slider:hover{border-color:#ddd}.gallery-thumb-slider.active,.gallery-thumb-slider.active:hover{border-color:#ff8d00}.gallery-thumb-back,.gallery-thumb-forward{width:20px;height:40px;background-color:#080808;position:absolute;top:0;cursor:pointer;opacity:.5;display:none}.gallery-thumb-back:hover,.gallery-thumb-forward:hover{opacity:1}.gallery-thumb-back{left:25%;margin-left:-24px}.gallery-thumb-forward{left:75%;margin-left:4px}.gallery-thumb-back:after{content:'';display:block;width:0;height:0;border:6px solid transparent;border-right-color:#999;position:absolute;left:0;top:14px}.gallery-thumb-forward:after{content:'';display:block;width:0;height:0;border:6px solid transparent;border-left-color:#999;position:absolute;left:8px;top:14px}.gallery-thumb-overflow .gallery-thumb-back,.gallery-thumb-overflow .gallery-thumb-forward{display:block}.gallery-thumb-overflow .gallery-thumb-slider{display:block;float:left}.gallery .gallery-header{display:block;position:absolute;width:100%;height:36px;line-height:36px;padding-left:15px;padding-right:36px;background-color:#fafafa}.gallery .gallery-header .gallery-btn-close,.gallery .gallery-header .gallery-btn-full{display:block;position:absolute;left:100%;top:0;text-align:center;cursor:pointer;opacity:.5;width:36px;height:36px;font-size:24px;line-height:34px;color:#000;font-weight:400}.gallery .gallery-header .gallery-btn-close:hover,.gallery .gallery-header .gallery-btn-full:hover{opacity:1;background-color:#f0f0f0}.gallery .gallery-header .gallery-btn-close:active,.gallery .gallery-header .gallery-btn-full:active{background-color:#eee}.gallery .gallery-header .gallery-btn-full:after{content:'';display:block;position:absolute;left:32%;top:32%;width:36%;height:36%;border:1px solid #333}.gallery .gallery-header .gallery-btn-full:after.resize{background-color:#fafafa}.gallery .gallery-header .gallery-btn-full:after:hover{background-color:#eee}.gallery .gallery-header .gallery-btn-full.resize:before{content:'';display:block;position:absolute;left:38%;top:24%;width:38%;height:38%;border:1px solid #000}.gallery .gallery-header .gallery-btn-full.resize:after{background-color:#fafafa}.gallery .gallery-header .gallery-btn-close{margin-left:-36px}.gallery .gallery-header .gallery-btn-full{margin-left:-72px}.gallery .gallery-body{position:absolute;left:0;right:0;top:0;bottom:0;overflow:hidden;text-align:center}.gallery.gallery-has-header .gallery-body{top:36px}.gallery.gallery-has-footer .gallery-body{bottom:36px}.gallery .gallery-img-container{display:block;position:absolute;left:0;top:0;width:100%;height:100%;-webkit-transition:left .5s,-webkit-transform .3s;transition:left .5s,transform .3s,-webkit-transform .3s;cursor:default}.gallery.lg,.gallery.md,.gallery.sm,.gallery.xs{background-color:transparent;border:1px solid #ddd;width:0;height:0}.gallery.gallery-fade-in.lg,.gallery.open.lg{width:80%;height:80%}.gallery.gallery-fade-in.md,.gallery.open.md{width:60%;height:60%}.gallery.gallery-fade-in.sm,.gallery.open.sm{width:40%;height:40%}.gallery.gallery-fade-in.xs,.gallery.open.xs{width:25%;height:25%}.gallery .gallery-close{display:block;width:50px;height:50px;text-align:center;line-height:50px;color:#fff;font-size:48px;position:absolute;right:0;top:0;cursor:pointer;opacity:.5}.gallery .gallery-close:hover{opacity:1}.gallery .gallery-footer{width:100%;height:36px;background-color:#fafafa;position:absolute;left:0;bottom:0;text-align:center;cursor:pointer}.gallery .gallery-enlarge{background:url(data:image/png;base64,) 0 0/54%}.gallery .gallery-reduce{background:url(data:image/png;base64,) 0 0/54%}.gallery .gallery-download{background:url(data:image/png;base64,) 0 0/40%}.gallery .gallery-rotate{background:url(data:image/png;base64,) 0 0/40%}.gallery .gallery-tool-item{display:inline-block;vertical-align:middle;width:36px;height:36px;text-align:center;line-height:36px;opacity:.75;position:relative;background-repeat:no-repeat;background-position:50% 50%;color:#333}.gallery .gallery-tool-item:hover{opacity:1}.gallery-scale-info{display:inline-block;text-align:center;position:relative;margin:10px auto;border-radius:15px;color:#fff;padding:4px 10px;background-color:rgba(0,0,0,.5);font-size:12px}`]
})
export class GalleryComponent implements OnInit, OnDestroy {
  @Input() data: any[] = [];//图片数据源
  @Input() dataProps: string[] = [];//表示图片地址的访问属性  如[a,b,c]表示.a.b.c
  @Input() size: any = '';//尺寸  xs md lg  默认全屏
  @Input() title: string = '';//标题
  @Input() isAnimation: boolean = true;//是否有动画
  @Input() isHeader: boolean = false;//是否显示头部
  @Input() isToolsBar: boolean = false;//是否显示（底部）工具栏
  @Input() isBtnDownload: boolean = false;//是否显示下载按钮

  @Output() change: EventEmitter<any> = new EventEmitter();//index change(将废弃)
  @Output() onChange: EventEmitter<any> = new EventEmitter();//index change
  @Output() onClose: EventEmitter<any> = new EventEmitter();//关闭
  @ViewChild('galleryBody') galleryBody: ElementRef;//组件内容区
  isFullScreen: boolean = false;//是否全屏
  images: {//格式化后的图片数据
    url: string;//地址
    scale: number;//缩放比例
    rotate: number;//旋转角度
    left: number,//左偏移
    top: number//右偏移
  }[] = [];//图片属性
  render: boolean = false;//是否渲染
  visible: boolean = false;//是否显示
  ready: boolean = false;//是否已就绪

  width: any = null;
  height: any = null;
  left: number | string = 0;//当前left
  top: number | string = 0;//当前top
  tempLeft: number | string = 0;//临时left 用于全屏/非全屏切换
  tempTop: number | string = 0;//临时top
  thumbScrollWidth: number = 0;//临时滚动宽度
  private transitionTime: number = 300;//过渡时间ms

  activeIndex: number = 0;//当前图片index

  transition: string = '';//'all '+transitionTime+'ms'+' ease-in-out'
  isThumbOverflow: boolean = false;//是否缩略图溢出
  isEventSource: boolean = false;//是否有事件源
  isShowBtns: boolean = false;//是否显示工具栏

  isSupportCssObjectFit: boolean = false;//是否支持css object-fit属性

  private resizeCheckTimer: any; //检测窗口改变的timer(=SetTimeout(...))
  private thumbSlideTimer: any;//缩略图滚动的timer
  private resizeHandler: any;//窗口大小改变处理函数
  private windowClickHandler: any;//窗口点击处理函数
  private keydownHandler: any;//按键处理
  private mouseWheelHandler: any;//鼠标滚动处理

  private isPressing = false;//是否在按着鼠标
  private tween = {//tween部分函数
    easeIn: function (t, b, c, d) {
      return c * (t /= d) * t + b;
    },
    easeOut: function (t, b, c, d) {
      return -c * (t /= d) * (t - 2) + b;
    },
    easeInOut: function (t, b, c, d) {
      if ((t /= d / 2) < 1) return c / 2 * t * t + b;
      return -c / 2 * ((--t) * (t - 2) - 1) + b;
    }
  };
  private bodyPadR = 0;//body paddingRight
  private mousePosition = {//鼠标在
    x: 0,
    y: 0
  };
  private mouseClickedPoint = {//鼠标按下去时的位置
    x: 0,
    y: 0
  };
  isShowScaleInfo = false;//是否显示缩放比例信息
  private hideScaleInfoTimer:any = 0;//隐藏缩放比例信息的timer
  constructor(private eleRef: ElementRef) {
    //窗口大小改变
    this.resizeHandler = () => {
      clearTimeout(this.resizeCheckTimer);
      this.resizeCheckTimer = setTimeout(() => {
        this.checkIsThumbOverflow();
      }, 300);
    };
    //窗口点击
    this.windowClickHandler = (ev:MouseEvent) => {
      if (!this.size && !this.isShowBtns) {
        this.close();
      }
    };
    //按下键盘
    this.keydownHandler = (ev: KeyboardEvent) => {
      if(this.images.length>1){
        const kc = ev.keyCode;
        if (kc === 37 || kc === 38) {
          this.prev();
          this.slideThumbAfterChange(0);
        } else if (kc === 39 || kc === 40) {
          this.next();
          this.slideThumbAfterChange(1);
        }
      }
    };
    //鼠标滚动
    this.mouseWheelHandler = (ev: any) => {
      let e = ev || window.event;
      if (this.visible) {
        //
        let mouseX = e['clientX'] || e['pageX'];
        let mouseY = e['clientY'] || e['pageY'];
        let gbody = this.galleryBody;
        if (gbody && gbody.nativeElement) {
          let rect = gbody.nativeElement.getBoundingClientRect();
          if (mouseX > rect.left && mouseX < rect.right && mouseY > rect.top && mouseY < rect.bottom) {
            if (e['wheelDelta'] < 0 || e['detail'] < 0) {//向下滚动 --缩小
              this.reduce(0.1);
            } else if (e['wheelDelta'] > 0 || e['detail'] > 0) {//向上滚动 --放大
              this.enlarge(0.1);
            }
          }
        }
      }
    };

  }

  ngOnInit() {
    let elem = document.createElement('IMG');
    if (elem.style['objectFit'] !== undefined) {
      this.isSupportCssObjectFit = true;
    }
    elem = null;
  }

  ngOnDestroy() {
    this.removeEvents();
  }

  /**
   * 鼠标在图片上按下时
   * @param ev 
   */
  onMouseDownImg(ev: MouseEvent) {
    ev.preventDefault();
    let p = this.getMousePosition(ev);
    this.mousePosition.x = p.left;
    this.mousePosition.y = p.top;
    this.mouseClickedPoint.x = p.left;
    this.mouseClickedPoint.y = p.top;
    this.isPressing = true;
  }

  /**
   * 鼠标弹起
   * @param ev 
   */
  onMouseUpImg(ev: MouseEvent) {
    ev.stopPropagation();
    let p = this.getMousePosition(ev);
    if (p.left == this.mouseClickedPoint.x && p.top == this.mouseClickedPoint.y) {
      this.toggleShowTools();
    }
    this.isPressing = false;
  }

  /**
   * 鼠标在图片上移动
   * @param ev 
   */
  onMouseMoveImg(ev: MouseEvent) {
    ev.preventDefault();
    if (this.isPressing) {//如果同时在按着鼠标(拖拽)
      let p = this.getMousePosition(ev);
      let img = this.images[this.activeIndex];
      let x = 2 * (p.left - this.mousePosition.x) + img.left;
      let y = 2 * (p.top - this.mousePosition.y) + img.top;
      if (img.rotate % 360 == 90) {
        x = 2 * (p.top - this.mousePosition.y) + img.left;
        y = -2 * (p.left - this.mousePosition.x) + img.top;
      } else if (img.rotate % 360 == 180) {
        x = -2 * (p.left - this.mousePosition.x) + img.left;
        y = -2 * (p.top - this.mousePosition.y) + img.top;
      } else if (img.rotate % 360 == 270) {
        x = -2 * (p.top - this.mousePosition.y) + img.left;
        y = 2 * (p.left - this.mousePosition.x) + img.top;
      }
      img.left = x;
      img.top = y;
      this.mousePosition.x = p.left;
      this.mousePosition.y = p.top;
    }
  }

  /**
   * 禁止事件冒泡
   * @param ev 
   */
  stopPropagation(ev: MouseEvent) {
    ev.stopPropagation();
  }

  /**
   * 清除事件
   */
  removeEvents() {
    //取消监听
    window.removeEventListener('resize', this.resizeHandler);
    window.removeEventListener('click', this.windowClickHandler);
    window.removeEventListener('keydown', this.keydownHandler);
    window.removeEventListener('mousewheel', this.mouseWheelHandler);
    window.removeEventListener('DOMMouseScroll', this.mouseWheelHandler);
  }

  /**
   * 检查是否溢出
   */
  private checkIsThumbOverflow() {
    let elem = this.eleRef.nativeElement.querySelector('.gallery-thumb-sliders');
    let check = () => {
      if (this.images.length > 1) {
        if (elem && elem['offsetWidth'] > 0) {
          this.isThumbOverflow = elem && (elem.scrollWidth > elem.clientWidth);
          if (this.isThumbOverflow) {
            let galleryThumbScroll = this.eleRef.nativeElement.querySelector('.gallery-thumb-scroll');
            let thumbSliders = galleryThumbScroll.querySelectorAll('.gallery-thumb-slider');
            if (thumbSliders.length > 0) {
              this.thumbScrollWidth = thumbSliders[0]['offsetWidth'] * thumbSliders.length;
              if (this.thumbScrollWidth < elem.scrollWidth) {
                this.isThumbOverflow = false;
              }
            }
          }
        } else {
          setTimeout(check, 10);
        }
      }
    };
    check();
  }


  /**
   * 初始化图片数据
   * @param data
   * @param props
   */
  private initImages(data: any[], props: string[]) {
    this.images = [];
    if (data.length > 0) {
      let images = [];
      for (let o of data) {
        let url = '';
        if (props.length > 0) {
          url = this.getValueByProps(o, props);
        } else if (typeof o == 'string') {
          url = o;
        }
        images.push({
          url: url,
          scale: 1,
          rotate: 0,
          left: 0,
          top: 0
        });
      }
      this.images = images;
    }
  }

  /**
   * 改变显示图片后滑动（如果需要）缩略图
   * @param direction 
   */
  slideThumbAfterChange(direction: number) {
    clearTimeout(this.thumbSlideTimer);
    this.thumbSlideTimer = setTimeout(() => {
      let sliderWrap = this.eleRef.nativeElement.querySelector('.gallery-thumb-sliders');
      if(sliderWrap){
        let perW = sliderWrap.offsetWidth;
        let scrollL = sliderWrap.scrollLeft;
        let sliderW = sliderWrap.clientWidth;
        if (direction === 0) {
          let refValue = (this.activeIndex) * perW;
          if (scrollL > refValue) {
            this.slide(sliderWrap, refValue - scrollL);
          }
        } else if (direction === 1) {
          let validScrollLeft = perW * (this.activeIndex + 1) - sliderW;
          if (validScrollLeft > scrollL) {
            this.slide(sliderWrap, validScrollLeft - scrollL);
          }
        }
      }
    });
  }

  /**
   * 缩略图滚动
   * @param elem
   * @param direction
   */
  slideThumb(elem: HTMLElement, direction: number) {
    var val = 0;
    var thumbItem = elem.querySelector('.gallery-thumb-slider');
    var thumbItemWidth = thumbItem['offsetWidth'];
    if (direction == 0) {
      val = -elem.clientWidth;
    } else {
      val = Math.floor(elem.clientWidth / thumbItemWidth) * thumbItemWidth;
    }
    var canScrollVal = elem.scrollWidth - elem.scrollLeft;
    if (val > canScrollVal) {
      val = Math.floor((canScrollVal) / thumbItemWidth) * thumbItemWidth;
    }
    this.slide(elem, val);
  }

  /**
   * 滚动
   * @param elem 滚动的元素
   * @param val  滚动值
   */
  slide(elem: HTMLElement, val: number) {
    var scrollLen = val;
    {
      var fps = 60;//帧数
      var run_time = 300;//执行时间
      var t_o = 1000 / fps;//每改变一次的时间间隔
      var t = 0;//开始时间
      var b = elem.scrollLeft;//初始值
      var c = scrollLen;//变化量
      var d = run_time / t_o;//次数
      var animate = this.tween.easeInOut;//选择算法
    }

    var timer = setInterval(function () {
      var newVal = Math.ceil(animate(t, b, c, d));
      elem.scrollLeft = newVal;
      if (t < d) {
        t++;
      } else {
        clearInterval(timer);
      }
    }, t_o);
  }

  /**
   * 通过属性名获取值
   * @param obj
   * @param pros
   * @returns any
   */
  private getValueByProps(obj: Object, pros: string[]): any {
    let result = obj;
    for (let prop of pros) {
      if (typeof result === 'object') {
        result = result[prop];
      }
    }
    return result;
  }

  /**
   * 打开
   */
  open(...args) {
    this.hideBodyScrollBar();
    this.render = true;
    this.isShowBtns = true;
    this.isFullScreen = false;
    this.isPressing = false;
    let dataIndex = 0;//索引
    let ev;//事件源

    let paramStrArr = [];//字符串数组参数容器
    let imgSrc = '';
    if (args && args.length > 0) {//参数处理
      for (let i in args) {
        if (args[i] instanceof MouseEvent) {//匹配事件源
          ev = args[i];
        }
        if (typeof args[i] === 'number') {//匹配index
          dataIndex = args[i];
        }
        if (args[i] instanceof Array) {//匹配
          paramStrArr.push(args[i]);
        }
        if (typeof args[i] === 'string') {//匹配单个图片地址或title
          if (!imgSrc) {//第一个字符串作为单个图片的地址
            imgSrc = args[i];
          } else {//n+1(n=0,1,2..)第2个以及之后的字符串作为title
            this.title = args[i];//title
          }
        }
      }
    }
    let dataObj = this.data;
    let dataProps = this.dataProps;
    if (imgSrc !== '') {
      dataObj = [imgSrc];
      dataProps = [];
      dataIndex = 0;
    } else {
      if (paramStrArr.length > 0) {
        dataObj = paramStrArr[0];
      }
      if (paramStrArr.length > 1) {
        dataProps = paramStrArr[1];
      }
    }
    this.initImages(dataObj, dataProps);
    let maxIndex = dataObj.length - 1;
    this.activate(dataIndex > maxIndex ? maxIndex : dataIndex);
    if (ev) {//根据事件源获取鼠标点击位置，从而从该位置弹出
      this.isEventSource = true;
      let pos = this.getMousePosition(ev),
        scrollElem = document.documentElement || document.body,
        scrollLeft = scrollElem.scrollLeft,
        scrollTop = scrollElem.scrollTop;
      this.tempLeft = (pos.left - scrollLeft) + 'px';
      this.tempTop = (pos.top - scrollTop) + 'px';
      this.left = this.tempLeft;
      this.top = this.tempTop;
    } else {
      this.isEventSource = false;
      this.tempLeft = '0';
      this.tempTop = '0';
    }
    let isAnimation = ((this.isAnimation + '') != 'false');
    {//就绪
      let delay = isAnimation ? this.transitionTime : 0;
      setTimeout(() => {
        this.ready = true;//就绪
        this.checkIsThumbOverflow();//检查是否溢出
      }, delay);
    }
    {//动画
      if ((this.isAnimation + '') != 'false') {
        setTimeout(() => {
          if (this.isEventSource) {
            this.transition = 'all ' + this.transitionTime + 'ms' + ' ease-in-out';
          } else {
            this.transition = 'opacity ' + this.transitionTime + 'ms' + ' ease-in-out';
          }
          this.show();
        }, 100);
      } else {
        this.show();
      }
    }

    this.addWindowListeners();
  }

  private show() {
    setTimeout(() => {//显示
      this.visible = true;
      this.setPositionAccordingSize();
    });
  }

  /**
   * 监听窗口事件
   */
  private addWindowListeners() {
    //监听窗口大小变化
    window.addEventListener('resize', this.resizeHandler);
    //点击窗口空白处
    setTimeout(() => {
      window.addEventListener('click', this.windowClickHandler);
    });
    //键盘控制
    setTimeout(() => {
      window.addEventListener('keydown', this.keydownHandler);
    });
    //
    setTimeout(() => {
      window.addEventListener('mousewheel', this.mouseWheelHandler);
      window.addEventListener('DOMMouseScroll', this.mouseWheelHandler);
    })
  }

  /**
   * 根据尺寸设置位置
   */
  private setPositionAccordingSize() {
    if (this.size == 'lg') {
      this.left = '10%';
      this.top = '10%';
    } else if (this.size == 'md') {
      this.left = '20%';
      this.top = '20%';
    } else if (this.size == 'sm') {
      this.left = '30%';
      this.top = '30%';
    } else if (this.size == 'xs') {
      this.left = '37.5%';
      this.top = '37.5%';
    } else {
      this.left = '0';
      this.top = '0';
    }
  }

  /**
   * 关闭
   */
  close() {
    this.reShowBodyScrollBar();
    this.visible = false;
    if (this.isEventSource) {
      this.left = this.tempLeft;
      this.top = this.tempTop;
    }
    this.ready = false;
    setTimeout(() => {
      this.render = false;
      this.transition = '';
    }, this.transitionTime);
    this.removeEvents();
    this.onClose.emit();
  }

  /**
   * 全屏切换
   */
  toggleFullScreen() {
    this.isFullScreen = !this.isFullScreen;
  }

  /**
   * 点击空白处
   */
  whiteSpaceClickAction() {
    if (!this.size) {
      this.close();
    }
  }

  /**
   * 最外层容器点击处理
   * @param ev 
   */
  wrapClickAction(ev: Event) {
    if (this.size) {
      ev.stopPropagation();
    }
  }


  /**
   * 上一张
   */
  prev() {
    if (this.activeIndex > 0) {
      this.activate(this.activeIndex - 1);
    }
  }

  /**
   * 下一张
   */
  next() {
    if (this.activeIndex < this.images.length - 1) {
      this.activate(this.activeIndex + 1);
    }
  }

  /**
   * 激活指定图片到当前窗口
   * @param index
   */
  activate(index: number) {
    this.activeIndex = index;
    this.change.emit(this.activeIndex);
    this.onChange.emit(this.activeIndex);
  }

  /**
   * 获取鼠标位置
   * @param e
   * @returns <{left: number; top: number}>
   */
  private getMousePosition(e: MouseEvent): { left: number, top: number } {
    var m_x = e.pageX || (e.clientX +
      (document.documentElement.scrollLeft
        || document.body.scrollLeft));
    var m_y = e.pageY || (e.clientY +
      (document.documentElement.scrollTop
        || document.body.scrollTop));
    return { left: m_x, top: m_y };
  }


  /**
   * 显示/隐藏工具
   */
  toggleShowTools() {
    this.isShowBtns = !this.isShowBtns;
  }

  /**
   * 放大
   * @param val 
   */
  enlarge(val: number) {
    let img = this.images[this.activeIndex];
    let scale = img.scale;
    scale += val;
    img.scale = scale;
    this.showScaleInfo();
  }
  /**
   * 缩小
   * @param val 
   */
  reduce(val: number) {
    let img = this.images[this.activeIndex];
    let scale = img.scale;
    if (scale > 0.1) {
      scale -= val;
    }
    img.scale = scale;
    this.showScaleInfo();
  }

  /**
   * 显示缩放比例信息
   */
  private showScaleInfo() {
    this.isShowScaleInfo = true;
    clearTimeout(this.hideScaleInfoTimer);
    this.hideScaleInfoTimer = setTimeout(() => {
      this.isShowScaleInfo = false;
    }, 1000);
  }

  /**
   * 旋转
   */
  rotate() {
    let img = this.images[this.activeIndex];
    img.scale = 1;
    img.left = 0;
    img.top = 0;
    let rotate = img.rotate;
    rotate += 90;
    img.rotate = rotate;
  }

  /**
   * 下载
   */
  download() {
    let url = this.images[this.activeIndex].url;
    const iframe = document.createElement('IFRAME');
    iframe.style.display = 'none';
    document.body.appendChild(iframe);
    iframe.setAttribute('src', url);
    setTimeout(() => {
      document.body.removeChild(iframe);
    }, 1000);
  }

  /**
   * 隐藏document.body的滚动条（如果有的话）
   */
  private hideBodyScrollBar() {
    let orgW = document.body.offsetWidth;//有滚动条时的宽度
    document.body.style.overflowY = 'hidden';
    let curW = document.body.offsetWidth;//无滚动条时的宽度
    let padR = document.body.style.paddingRight.replace('px', '');
    if (padR) {
      this.bodyPadR = parseFloat(padR);
    }
    if (curW > orgW) {
      document.body.style.paddingRight = this.bodyPadR + (curW - orgW) + 'px';//给body设置paddingRight避免页面抖动
    }
  }

  /**
   * 重新显示document.body的滚动条（如果有的话）
   */
  private reShowBodyScrollBar() {
    document.body.style.overflowY = null;
    document.body.style.paddingRight = (this.bodyPadR ? this.bodyPadR + 'px' : null);
  }
}
